export default {
	data() {
		return {
			preVASList:{},
			finishVASList:null,
			size:10,
			skip:0,
			footerShow:false
		}
	},
	onShow() {
		this.getPreVASList()
		this.getFinishVASList()
	},
	onHide(){
		this.size = 10
		this.skip = 0
		this.finishVASList = null
	},
	onUnload(){
		this.size = 10
		this.skip = 0
		this.finishVASList = null
	},
	onReachBottom(){
		console.log("触底")
		this.skip = this.skip+10;
		this.getFinishVASList()
	},
	methods: {
		async getPreVASList() {
			try {
				let data = await this.$wxhttp.post({
					url: '/vas/v1/getPreVASList'
				})
				this.preVASList = data.data;
			} catch (e) {
				console.log(e)
			}
		},
		async getFinishVASList() {
			try {
				let data = await this.$wxhttp.post({
					url: '/vas/v1/getFinishVASList',
					data:{
						size:this.size,
						skip:this.skip,
					}
				})
				let finishVASList = this.finishVASList;
				if( finishVASList != null ){
					let list = finishVASList.vas_list;
					let newLIst = data.data.vas_list
					if( newLIst.length > 0 ){
						newLIst.map(item=>{
							list.push(item)
						})
						this.finishVASList = finishVASList
					}else{
						this.footerShow = true
					}
					
				}else{
					this.finishVASList = data.data;
				}
				
			} catch (e) {
				console.log(e)
			}
		},
	}
}
